.sdoc-outline-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  margin: 20px 30px 20px 16px;
  min-height: 0;
  z-index: 101;
}

.sdoc-outline {
  flex: 1;
  width: 220px; 
  display: flex;
  min-height: 0;
  flex-direction: column;
  font-size: 14px;
  position: relative;
}

.sdoc-outline-header  {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.25rem 0;
  color: #999;
  border-bottom: 1px solid #dbdbdb;
}

.sdoc-outline-header__title {
  font-size: 14px;
  line-height: 1.1;
  margin: 0 !important;
}

.sdoc-outline-header__close {
  cursor: pointer;
  font-size: 14px;
}

.sdoc-outline-header__close:hover {
  color: #555;
}

.sdoc-outline-list-container {
  list-style: none;
  padding: 0.5rem 0;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: hidden;
}

.sdoc-outline-list-container:hover {
  overflow-y: auto;
}

.sdoc-outline-item {
  padding: 4px 0;
  cursor: pointer;
}

.sdoc-outline-item.active {
  color: #ff8000;
}

.sdoc-outline-menu {
  line-height: 1;
  font-size: 14px;
  color: #888;
  cursor: pointer;
  width: 28px;
  height: 28px;
  background: #fff;
  border-radius: 0 50% 50% 0;
  box-shadow: 0 0 6px rgba(0,0,0, 0.12);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 20px;
  left: -16px;
}

.sdoc-outline-menu.disabled {
  opacity: .65;
}

.sdoc-outline-menu:not(.disabled):hover {
  color: #333;
}
