.sdoc-editor-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.sdoc-editor-container .sdoc-editor-toolbar {
  display: flex;
  justify-content: center;
  height: 44px;
  align-items: center;
  padding: 0 10px;
  background-color: #fff;
  user-select: none;
  border-bottom: 1px solid #e5e6e8;
  position: relative;
  z-index: 102;
}

.sdoc-editor-container .sdoc-editor-content {
  width: 100%;
  height: calc(100% - 44px);
  display: flex;
  background: #f5f5f5;
  position: relative;
}

.sdoc-editor-container .sdoc-absolute-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}

.sdoc-editor-container .sdoc-scroll-container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  overflow: auto;
}

.sdoc-editor-container .sdoc-article-container {
  position: relative;
  top: 0;
  width: 794px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
}

.sdoc-editor-container .sdoc-editor-content .article {
  width: 794px;
  min-height: 800px;
  padding: 40px 60px;
  background-color: #fff;
  border: 1px solid #e5e6e8;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.06);
}

.sdoc-editor-container .sdoc-editor-content .article > div {
  caret-color: blue;
}

.sdoc-editor-container .sdoc-editor-content .article .sdoc-drag-cover::before {
  content: ' ';
  width: 59px;
  display: inline-block;
  position: absolute;
  left: -60px;
}

.sdoc-editor-container .sdoc-editor-content .article .sdoc-draging {
  border-bottom: 2px solid rgba(35,131,226);
}

.sdoc-editor-container .seafile-block-container {
  position: relative;
}

.sdoc-editor-container .seafile-block-container .comment-count {
  position: absolute;
  right: -55px;
  top: 0px;
  border-radius: 4px;
  width: 15px;
  height: 15px;
  line-height: 18px;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: rgba(0, 0, 0, .3);
}

.sdoc-editor-container .seafile-block-container .comment-count__btn {
  font-size: 12px;
  transform: scale(.75);
}
