.collaborators-popover.popover {
  width: 300px;
  max-width: 300px;
}

.collaborators-popover .popover-container {
  padding: 0;
  margin: 0;
  max-height: 360px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.collaborators-popover .popover-header {
  height: 50px;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
  border-bottom: 1px solid #ededed;
  color: #212529;
  background: #fff;
}

.collaborators-popover .popover-container .content-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow: auto;
  padding: 5px 0;
}

.content-list .collaborator-details {
  height: 32px;
  line-height: 32px;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 3px 12px;
  
}

.content-list .collaborator-details:hover {
  background-color: #f8f8f8;
}

.content-list .collaborator-details .collaborator-tag {
  margin-right: 8px;
  height: 5px;
  width: 5px;
  background-color: #08DB6B;
  border-radius: 50%;
  overflow: hidden;
}

.content-list .collaborator-details .collaborator-avatar {
  margin-right: 8px;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  overflow: hidden;
}

.content-list .collaborator-details .collaborator-name {
  max-width: calc(100% - 20px);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #212529;
}
