.sdoc-code-block-container {
  position: relative;
}

.sdoc-code-block-pre {
  background: #FAFAFA;
  border: 1px solid #f0f0f0;
}

.sdoc-code-block-pre .sdoc-code-no-wrap {
  white-space: nowrap;
}

.sdoc-code-block-pre .sdoc-code-no-wrap .sdoc-code-line > span {
  white-space: pre;
}

.sdoc-code-block-code .sdoc-code-line {
  position: relative;
  counter-increment: number;
  margin-left: 32px;
}

.sdoc-code-block-code .sdoc-code-line::before {
  content: counter(number);
  color: #ccc;
  width: 40px;
  text-align: end;
  position: absolute;
  left: -50px;
}

.sdoc-code-line
.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: slategray;
}

.sdoc-code-line
.token.punctuation {
  color: #999;
}

.sdoc-code-line
.token.namespace {
  opacity: .7;
}

.sdoc-code-line
.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #905;
  padding: 0rem;
}

.sdoc-code-line
.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #690;
}

.sdoc-code-line
.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #9a6e3a;
}

.sdoc-code-line
.token.atrule,
.token.attr-value,
.token.keyword {
  color: #07a;
}

.sdoc-code-line
.token.function,
.token.class-name {
  color: #DD4A68;
}

.sdoc-code-line
.token.regex,
.token.important,
.token.variable {
  color: #e90;
}

.sdoc-code-line
.sdoc-code-line
.token.important,
.token.bold {
  font-weight: bold;
}

.sdoc-code-line
.token.italic {
  font-style: italic;
}

.sdoc-code-line
.token.entity {
  cursor: help;
}
