.sdoc-comment-list-container {
  position: absolute;
}

.sdoc-comment-list-container .comment-ui-container {
  background-color: #edf2fa;
  border-radius: 8px;
  box-shadow: 0 0 2px rgba(0,0,0,.04);
  padding: 16px 0;
  margin-bottom: 10px;
  cursor: pointer;
}

.sdoc-comment-list-container .comment-ui-container.active {
  position: relative;
  left: -24px;
  background: rgba(255, 255, 255, .92);
  box-shadow: 0 1px 3px rgba(0,0,0,.3), 0 4px 8px 3px rgba(0,0,0,.15);
}

.sdoc-comment-list-container .comment-item-selected-text-container {
  width: 100%;
  height: 32px;
  display: flex;
  padding: 6px 16px;
  align-items: center;
  overflow: hidden;
  border-radius: 8px 8px 0 0;
}

.sdoc-comment-list-container .comment-item-selected-text-container .sdoc-comment-quote {
  font-size: 12px;
  flex-shrink: 0;
}

.sdoc-comment-list-container .comment-item-selected-text-container .comment-item-selected-text {
  flex: 1;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sdoc-comment-list-container .comment-item-list {
  max-height: 350px;
  min-width: 280px;
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0;
}

.sdoc-comment-list-container .comment-item {
  position: relative;
  padding: 16px 16px 0;
  cursor: pointer;
}

.sdoc-comment-list-container .comment-delete-shadow {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, .7);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  color: #fff;
  z-index: 1;
}

.sdoc-comment-list-container .comment-delete-shadow .delete-tip {
  margin-bottom: 10px;
}

.sdoc-comment-list-container .comment-ui-container.active .comment-item:hover {
  background: #fafafa;
}

.sdoc-comment-list-container .comment-item .comment-header,
.sdoc-comment-list-container .comment-item .comment-author {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sdoc-comment-list-container .comment-item .comment-content {
  padding-bottom: 10px;
  margin-top: 10px;
  margin-left: 30px;
  width: 222px;
  word-break: break-all;
}

.sdoc-comment-list-container .comment-header .comment-author__avatar {
  display: flex;
  align-items: center;
}

.sdoc-comment-list-container .comment-header .comment-author__avatar img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.sdoc-comment-list-container .comment-header .comment-author__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: pointer;
}

.sdoc-comment-list-container .comment-header .comment-author__info .name {
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
}

.sdoc-comment-list-container .comment-header .comment-author__info .time {
  color: #444746;
  font-size: 12px;
  line-height: 16px;
  display: inline-flex;
  align-items: center;
}

.sdoc-comment-list-container .comment-header .sdoc-unread-message-tip {
  display: inline-block;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: #fc6440;
  margin-left: 6px;
}

.sdoc-comment-list-container .comment-header .sdoc-unread-message-text-tip {
  display: none;
  height: 16px;
  padding: 0 5px;
  border-radius: 8px;
  margin-left: 6px;
  background-color: #fc6440;
  color: #fff;
}

.sdoc-comment-list-container .comment-ui-container:hover .comment-header .sdoc-unread-message-tip {
  display: none;
}

.sdoc-comment-list-container .comment-ui-container:hover .comment-header .sdoc-unread-message-text-tip {
  display: inline-block;
}

.sdoc-comment-list-container .comment-ui-container .comment-operation {
  width: 32px;
  height: 32px;
  padding: 0 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  color: rgb(70, 77, 90);
  overflow: hidden;
}

.sdoc-comment-list-container .comment-ui-container.active .comment-operation:hover {
  cursor: pointer;
  background-color: #f0f0f0;
}

.sdoc-comment-list-container .comment-ui-container .comment-operation .sdoc-confirm {
  color: #20a0ff;
  font-weight: 800;
}

.sdoc-comment-menu {
  padding: 0;
  border-radius: 2px;
}

.sdoc-popover-menu {
  background: rgb(255, 255, 255);
  width: 140px;
  padding: 8px 0;
  height: auto;
}

.sdoc-popover-menu__item {
  padding: 4px 24px;
  height: 28px;
  vertical-align: middle;
  font-size: 14px;
  background-color: transparent;
  cursor: default;
  color: #202428;
  background-color: #fff;
}

.sdoc-popover-menu__item:hover {
  background-color: #20a0ff;
  color: #fff;
}

.sdoc-comment-list-container .comment-editor-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 16px;
  cursor: text;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor {
  margin-bottom: 10px;
  min-height: 40px;
  max-height: 120px;
  min-width: 240px;
  overflow: auto;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  padding: 8px;
  word-break: break-all;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor:empty:before {
  content: attr(placeholder);
  opacity: .6;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor:focus-visible {
  outline: none;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor:focus {
  border: 1px solid rgba(0, 0, 0, .12);
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor-user-info {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor-user-info .comment-editor-user-img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 8px;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-editor-user-info .comment-editor-user-name {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  user-select: none;
}

.sdoc-comment-list-container .comment-editor-wrapper .comment-operations {
  display: flex;
  justify-content: flex-end;
}
