/* modify default css */
.sdoc-editor-container .article {
  font-size: 11pt;
}

.sdoc-editor-container .sdoc-code-block-pre {
  font-size: 10pt;
}

.sdoc-editor-container .article p {
  padding: 5px 0;
  margin: 0;
}

.element-icon {
  color: #444 !important;
}

.sdoc-icon-btn {
  height: 24px;
  width: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sdoc-icon-btn:hover {
  cursor: pointer;
}

.sdoc-icon-btn .sdocfont {
  color: #999;
  font-size: 16px;
}

.sdoc-icon-btn:hover .sdocfont {
  color: #5a5a5a;
}
