/* header */
.sdoc-editor-container .article .sdoc-header-title,
.sdoc-editor-container .article .sdoc-header-subtitle,
.sdoc-editor-container .article .sdoc-header-1,
.sdoc-editor-container .article .sdoc-header-2,
.sdoc-editor-container .article .sdoc-header-3,
.sdoc-editor-container .article .sdoc-header-4,
.sdoc-editor-container .article .sdoc-header-5,
.sdoc-editor-container .article .sdoc-header-6 {
  line-height: 1.1;
  margin: 1.2em 0 0.4em;
  font-weight: bold;
}

.sdoc-editor-container .article .sdoc-header-2,
.sdoc-editor-container .article .sdoc-header-3 {
  line-height: 1.5;
}

.sdoc-editor-container .article .sdoc-header-4 {
  line-height: 1.125;
}

/* list  */
.sdoc-editor-container .article .list-container {
  margin: 0;
  /* override seafile-ui padding */
  padding-left: 1.2em !important;
}

.sdoc-editor-container .article .list-container .sdoc-li-blod::marker {
  font-weight: bold;
}

/* check list  */
.sdoc-editor-container .article .sdoc-checkbox-container {
  padding: 0px 2px;
}

.sdoc-editor-container .article .sdoc-checkbox-container .sdoc-checkbox-input-wrapper {
  display: flex;
  align-items: baseline;
}

.sdoc-editor-container .article .sdoc-checkbox-container .sdoc-checkbox-input-wrapper .sdoc-checkbox-input {
  vertical-align: middle;
  margin-right: 5px;
}

.sdoc-editor-container .article .sdoc-checkbox-container .sdoc-checkbox-input-wrapper .sdoc-checkbox-content-container {
  word-break:normal;
  width: calc(100% - 1em);
}

/* image */
.sdoc-editor-container .article .sdoc-image-wrapper {
  position: relative;
  display: inline-block;
  padding: 6px 6px 6px 0;
}

.sdoc-editor-container .article .sdoc-image-inner {
  position: relative;
  display: inline-block;
}

.sdoc-editor-container .article .sdoc-image-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sdoc-editor-container .article .sdoc-image-content :first-child {
  position: relative;
}

.sdoc-editor-container .article .sdoc-image-content .sdoc-image-caption-input-wrapper {
  border: 0;
  outline: none;
  margin-top: 8px;
  color: #8A8F8D;
  background-color: transparent;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sdoc-editor-container .article .sdoc-image-content .sdoc-image-caption-input-wrapper::placeholder {
  color: #bec0bf
}

.sdoc-editor-container .article .image-selected {
  box-shadow: 0 0 0 2px #007bff;
}

.sdoc-editor-container .article .image-resizer {
  width: 10px;
  height: 10px;
  position: absolute;
  right: -5px;
  bottom: -5px;
  background-color: #007bff;
  border: 1px solid #fff;
  cursor: se-resize;
}

.sdoc-editor-container .article .image-full-screen {
  position: absolute;
  height: 26px;
  display: inline-block;
  width: 26px;
  top: 0;
  right: -32px;
  text-align: center;
  line-height: 20px;
  padding: 2px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  border: 1px solid rgba(0, 40, 100, 0.12);
  background-color: #fff;
  user-select: none !important;
  z-index: 1000;
}

.sdoc-editor-container .article .image-full-screen:hover {
  display: block;
  background-color: #efefef;
}

.sdoc-editor-container .article .image-size {
  display: inline-block;
  padding: 5px;
  white-space: nowrap;
  height: 22px;
  position: absolute;
  bottom: -25px;
  left: 100%;
  z-index: 1;
  transform: translateX(5px);
  border-radius: 3px;
  line-height: 12px;
  color: #fff;
  background-color: #4c4c4c;
  font-size: 12px;
}

/* caret */
.sdoc-editor-container .article .caret-item .caret-name::before {
  content: attr(name);
}

.sdoc-editor-container .article .caret-item .caret-name {
  display: none;
}

.sdoc-editor-container .article .caret-item:hover .caret-name {
  display: block;
}

/* link */
.sdoc-editor-container .article .virtual-link {
  color: #eb8205;
}

.sdoc-editor-container .article .virtual-link:hover {
  text-decoration: underline;
  text-underline-position: under;
}
