.doc-ops {
  display: flex;
  align-items: center;
}

.doc-ops .op-item {
  margin-left: 1rem;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.doc-ops .op-item .sdocfont {
  font-size: 18px;
  color: #666;
  cursor: pointer;
}

.doc-ops .op-item .sdocfont:hover {
  color: #333;
  cursor: pointer;
}

.doc-ops .popover-wrapper {
  width: 300px;
}
