.sdoc-tag-popover {
  padding-bottom: 0;
  width: 230px;
}

.sdoc-popover-container .sdoc-tag-search-input {
  margin-bottom: 10px;
}

.sdoc-popover-container .sdoc-tag-list-container {
  max-height: 180px;
  overflow-y: auto;
}

.sdoc-tag-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px 10px;
  width: 100%;
  height: 30px;
  cursor: pointer;
}

.sdoc-tag-item:hover {
  background-color: #eee;
}

.sdoc-tag-item .sdoc-tag-badge-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sdoc-tag-item .sdoc-tag-badge {
  margin-right: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 13px;
  font-weight: normal;
}

.sdoc-tag-item .sdoc-tag-added-mark {
  width: 18px;
}

.sdoc-create-tag {
  padding: 8px 10px;
  border-top: 1px solid #eee;
  border-bottom: none;
  overflow: hidden;
  font-size: 14px;
  white-space: nowrap;
  font-weight: normal;
  text-overflow: ellipsis;
  background-color: #fff;
  cursor: pointer;
}

.sdoc-create-tag:hover {
  background-color: #f5f5f5;
}

.sdoc-create-tag .add-icon {
  margin-right: 6px;
  font-size: 13px;
  font-weight: 600;
}
